.slick-prev, .slick-next { position: absolute; border: solid #fff; opacity: 0.9; border-width: 0 13px 13px 0; display: inline-block; height: 36px; width: 36px; line-height: 0px; font-size: 0px; cursor: pointer; background: transparent; top: 50%; margin-top: -20px; padding: 0; z-index: 999; outline: none; }

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus { outline: none; background: transparent; color: transparent; opacity: 0.7; }

