@include font(Bebas, Bebas);

a {
    color: #000;
    text-decoration: none; }

.container {

	width: 960px;
	margin-right: auto;
	margin-left: auto;
	padding-left: 10px;
	padding-right: 10px;

	@include rmin(200) {
		width: 200px; }
	@include rmin(320) {
		width: 80%; }
	@include rmin(480) {
		width: 90%; }
	@include rmin(940) {
		width: 80%; }
	@include rmin(1100) {
		width: 65%; }
	@include rmin(1600) {
		width: 50.5%; } }

.container--flex {
	@include flex; }

.main-container {
	position: relative;

	@include rmin(320) {
		margin-right: 0; }
	@include rmin(480) {
		width: 71.5%;
		margin-right: 25px; }
	@include r(480) {
		width: 100%; } }

.blog-h2 {
	color: $dark-orange;
	text-transform: uppercase;
	font-family: Bebas;
	@include fs(18);
	font-weight: 400;
	a {
		color: $dark-orange; } }

.pages-number {
	@include flex;
	margin-left: 5px;
	margin-bottom: 80px;

	span {
		display: block;
		height: 25px;
		width: 25px;
		font-family: Bebas;
		text-align: center;
		margin-right: 4px;
		color: #FFF;
		background-color: $dark-orange;
		opacity: 0.6;
		&:hover {
			opacity: 0.8; } }

	.is-active {
		span {
			opacity: 1; } } }

.division-line {
	background-image: url(../../img/division-line.png);
	height: 13px;
	width: 100%;
	margin-top: 3px; }

.single-article-content {
	.blog-info {
		margin-top: 10px; }

	.division-line {
		@include rmin(320) {
			margin-top: 17px;
			margin-bottom: 28px; }
		@include rmin(700) {
			margin-top: 47px;
			margin-bottom: 48px; } } }



